import React from "react";
import {
  TextField,
  Button,
  LinearProgress,
  Typography,
} from "@material-ui/core";

class TransmitterConfig extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      gain: 60.4,
      cFreq: "5.22e9",
      sampleRate: "20e6",
      transmitting: false,
    };
  }

  componentDidMount() {}

  render() {
    return (
      <div
        style={{
          height: "50vh",
          width: "40vw",
          boxShadow:
            "rgb(145 158 171 / 24%) 0px 0px 2px 0px, rgb(145 158 171 / 24%) 0px 16px 32px -4px",
          borderRadius: "2%",
          position: "relative",
        }}
      >
        <div
          style={{
            height: "50%",
            padding: "10%",
          }}
        >
          <Typography variant="h6" style={{ paddingBottom: "15px" }}>
            Enter USRP Transmitter Parameters
          </Typography>

          <TextField
            label="Gain"
            placeholder="20"
            variant="outlined"
            fullWidth
            type="number"
            value={this.state.gain}
            onChange={(event) => this.setState({ gain: event.target.value })}
            style={{ paddingBottom: "4%" }}
          />
          <TextField
            label="Center Frequency"
            variant="outlined"
            placeholder="5.22e9"
            fullWidth
            value={this.state.cFreq}
            onChange={(event) => this.setState({ cFreq: event.target.value })}
            style={{ paddingBottom: "4%" }}
          />
          <TextField
            label="Sample Rate"
            variant="outlined"
            placeholder="20e6"
            fullWidth
            value={this.state.sampleRate}
            onChange={(event) =>
              this.setState({ sampleRate: event.target.value })
            }
            style={{
              paddingBottom: "4%",
            }}
          />
        </div>

        <div
          style={{
            height: "20%",
            paddingLeft: "10%",
            paddingRight: "10%",
            position: "absolute",
            width: "80%",
            bottom: 0,
          }}
        >
          <Button
            variant="contained"
            fullWidth
            color={this.state.transmitting ? "secondary" : "primary"}
            onClick={() =>
              this.setState({ transmitting: !this.state.transmitting })
            }
          >
            {this.state.transmitting ? "Stop Transmitting" : "Transmit"}
          </Button>
          {this.state.transmitting && (
            <LinearProgress color="primary" style={{ color: "#33eb91" }} />
          )}
        </div>
      </div>
    );
  }
}

export default TransmitterConfig;
